import { each, isNil } from '@antv/util'; // ab x^^a = b;x
//  b  

export function calBase(a, b) {
  var e = Math.E;
  var value;

  if (b >= 0) {
    value = Math.pow(e, Math.log(b) / a); // 
  } else {
    value = Math.pow(e, Math.log(-b) / a) * -1; // 
  }

  return value;
}
export function log(a, b) {
  if (a === 1) {
    return 1;
  }

  return Math.log(b) / Math.log(a);
}
export function getLogPositiveMin(values, base, max) {
  if (isNil(max)) {
    max = Math.max.apply(null, values);
  }

  var positiveMin = max;
  each(values, function (value) {
    if (value > 0 && value < positiveMin) {
      positiveMin = value;
    }
  });

  if (positiveMin === max) {
    positiveMin = max / base;
  }

  if (positiveMin > 1) {
    positiveMin = 1;
  }

  return positiveMin;
}