/**
 * @ignore
 * 
 * path 
 * @param element 
 * @param animateCfg 
 * @param cfg 
 */
export function pathIn(element, animateCfg, cfg) {
  // @ts-ignore
  var length = element.getTotalLength(); // 

  element.attr('lineDash', [length]);
  element.animate(function (ratio) {
    return {
      // 
      lineDashOffset: (1 - ratio) * length
    };
  }, animateCfg);
}