import { last } from '@antv/util';
import catTicks from './cat';
/**
 *  ticks, 
 * @param cfg 
 * @returns  ticks
 */

export default function calculateTimeCatTicks(cfg) {
  var ticks = catTicks(cfg);
  var lastValue = last(cfg.values);

  if (lastValue !== last(ticks)) {
    ticks.push(lastValue);
  }

  return ticks;
}