function parseAnimateConfig(animateCfg, data) {
  return {
    delay: isFunction(animateCfg.delay) ? animateCfg.delay(data) : animateCfg.delay,
    easing: isFunction(animateCfg.easing) ? animateCfg.easing(data) : animateCfg.easing,
    duration: isFunction(animateCfg.duration) ? animateCfg.duration(data) : animateCfg.duration,
    callback: animateCfg.callback
  };
}
/**
 * @ignore
 *  elementName  `animateType` `animateType` 
 * @param elementName 
 * @param coordinate 
 * @param animateType 
 */


export function getDefaultAnimateCfg(elementName, coordinate, animateType) {
  var animateCfg = GEOMETRY_ANIMATE_CFG[elementName];

  if (animateCfg) {
    if (isFunction(animateCfg)) {
      animateCfg = animateCfg(coordinate);
    }

    animateCfg = deepMix({}, DEFAULT_ANIMATE_CFG, animateCfg);

    if (animateType) {
      return animateCfg[animateType];
    }
  }

  return animateCfg;
}
/**
 * @ignore
 * 
 *  shape 
 * @param shape 
 * @param animateCfg 
 * @param cfg 
 */

export function doAnimate(shape, animateCfg, cfg) {
  var data = get(shape.get('origin'), 'data', FIELD_ORIGIN);
  var animation = animateCfg.animation; // 

  var parsedAnimateCfg = parseAnimateConfig(animateCfg, data);

  if (animation) {
    // 
    var animateFunction = getAnimation(animation);

    if (animateFunction) {
      animateFunction(shape, parsedAnimateCfg, cfg);
    }
  } else {
    //  toAttrs 
    shape.animate(cfg.toAttrs, parsedAnimateCfg);
  }
}
/**
 * @ignore
 *  Geometry 
 * @param container 
 * @param animateCfg 
 * @param geometryType geometry 
 * @param coordinate 
 * @param minYPoint y 
 */

export function doGroupAppearAnimate(container, animateCfg, geometryType, coordinate, minYPoint) {
  if (GEOMETRY_GROUP_APPEAR_ANIMATION[geometryType]) {
    var defaultCfg = GEOMETRY_GROUP_APPEAR_ANIMATION[geometryType](coordinate);
    var animation = getAnimation(get(defaultCfg, 'animation', ''));

    if (animation) {
      var cfg = __assign(__assign(__assign({}, DEFAULT_ANIMATE_CFG.appear), defaultCfg), animateCfg);

      container.stopAnimate(); //  container 

      animation(container, cfg, {
        coordinate: coordinate,
        minYPoint: minYPoint,
        toAttrs: null
      });
    }
  }
}