import { each, isArray } from '@antv/util';
import { catmullRom2bezier, getLinePath } from '../../geometry/shape/util/path';
import { toPoints } from '../../util/bbox';
import isPolygonsIntersect from '@antv/path-util/lib/is-polygons-intersect';

function getMaskBBox(context, tolerance) {
  var event = context.event;
  var maskShape = event.target;
  var maskBBox = maskShape.getCanvasBBox(); //  bbox 

  if (!(maskBBox.width >= tolerance || maskBBox.height >= tolerance)) {
    return null;
  }

  return maskBBox;
}

function getMaskPath(context, tolerance) {
  var event = context.event;
  var maskShape = event.target;
  var maskBBox = maskShape.getCanvasBBox(); //  bbox 

  if (!(maskBBox.width >= tolerance || maskBBox.height >= tolerance)) {
    return null;
  }

  return maskShape.attr('path');
}
/**
 * 
 * @param context 
 * @ignore
 */


export function getCurrentElement(context) {
  var event = context.event;
  var element;
  var target = event.target;

  if (target) {
    element = target.get('element');
  }

  return element;
}
/**
 * 
 * @param context 
 * @ignore
 */

export function getDelegationObject(context) {
  var event = context.event;
  var target = event.target;
  var delegateObject;

  if (target) {
    delegateObject = target.get('delegateObject');
  }

  return delegateObject;
}
export function isElementChange(context) {
  var event = context.event.gEvent; //  element label  shape 

  if (event && event.fromShape && event.toShape && event.fromShape.get('element') === event.toShape.get('element')) {
    return false;
  }

  return true;
}
/**
 * 
 * @param delegateObject 
 * @ignore
 */

export function isList(delegateObject) {
  return delegateObject && delegateObject.component && delegateObject.component.isList();
}
/**
 * 
 * @param delegateObject 
 * @ignore
 */

export function isSlider(delegateObject) {
  return delegateObject && delegateObject.component && delegateObject.component.isSlider();
}
/**
 *  mask 
 * @param context 
 * @ignore
 */

export function isMask(context) {
  var event = context.event;
  var target = event.target;
  return target && target.get('name') === 'mask';
}
/**
 *  elements
 * @param context 
 * @ignore
 */

export function getMaskedElements(context, tolerance) {
  var target = context.event.target;

  if (target.get('type') === 'path') {
    var maskPath = getMaskPath(context, tolerance);

    if (!maskPath) {
      return;
    }

    return getElementsByPath(context.view, maskPath);
  }

  var maskBBox = getMaskBBox(context, tolerance); //  bbox 

  if (!maskBBox) {
    return null;
  }

  return getIntersectElements(context.view, maskBBox);
}
/**
 * @ignore
 */

export function getSiblingMaskElements(context, sibling, tolerance) {
  var maskBBox = getMaskBBox(context, tolerance); //  bbox 

  if (!maskBBox) {
    return null;
  }

  var view = context.view;
  var start = getSiblingPoint(view, sibling, {
    x: maskBBox.x,
    y: maskBBox.y
  });
  var end = getSiblingPoint(view, sibling, {
    x: maskBBox.maxX,
    y: maskBBox.maxY
  });
  var box = {
    minX: start.x,
    minY: start.y,
    maxX: end.x,
    maxY: end.y
  };
  return getIntersectElements(sibling, box);
}
/**
 * 
 * @param view View/Chart
 * @ignore
 */

export function getElements(view) {
  var geometries = view.geometries;
  var rst = [];
  each(geometries, function (geom) {
    var elements = geom.elements;
    rst = rst.concat(elements);
  });

  if (view.views && view.views.length) {
    each(view.views, function (subView) {
      rst = rst.concat(getElements(subView));
    });
  }

  return rst;
}
/**
 * 
 * @param view View/Chart
 * @param field 
 * @param value 
 * @ignore
 */

export function getElementsByField(view, field, value) {
  var elements = getElements(view);
  return elements.filter(function (el) {
    return getElementValue(el, field) === value;
  });
}
/**
 * 
 * @param view View/Chart
 * @param stateName 
 * @ignore
 */

export function getElementsByState(view, stateName) {
  var geometries = view.geometries;
  var rst = [];
  each(geometries, function (geom) {
    var elements = geom.getElementsBy(function (el) {
      return el.hasState(stateName);
    });
    rst = rst.concat(elements);
  });
  return rst;
}
/**
 * 
 * @param element 
 * @param field 
 * @ignore
 */

export function getElementValue(element, field) {
  var model = element.getModel();
  var record = model.data;
  var value;

  if (isArray(record)) {
    value = record[0][field];
  } else {
    value = record[field];
  }

  return value;
}
/**
 * 
 * @param box1 1
 * @param box2 2
 * @ignore
 */

export function intersectRect(box1, box2) {
  return !(box2.minX > box1.maxX || box2.maxX < box1.minX || box2.minY > box1.maxY || box2.maxY < box1.minY);
}
/**
 * 
 * @param view View/Chart
 * @param box 
 * @ignore
 */

export function getIntersectElements(view, box) {
  var elements = getElements(view);
  var rst = [];
  each(elements, function (el) {
    var shape = el.shape;
    var shapeBBox = shape.getCanvasBBox();

    if (intersectRect(box, shapeBBox)) {
      rst.push(el);
    }
  });
  return rst;
}

function pathToPoints(path) {
  var points = [];
  each(path, function (seg) {
    var command = seg[0];

    if (command !== 'A') {
      for (var i = 1; i < seg.length; i = i + 2) {
        points.push([seg[i], seg[i + 1]]);
      }
    } else {
      var length_1 = seg.length;
      points.push([seg[length_1 - 2], seg[length_1 - 1]]);
    }
  });
  return points;
}
/**
 * 
 * @param view View/Chart
 * @param path 
 * @ignore
 */


export function getElementsByPath(view, path) {
  var elements = getElements(view);
  var points = pathToPoints(path);
  var rst = elements.filter(function (el) {
    var shape = el.shape;
    var shapePoints;

    if (shape.get('type') === 'path') {
      shapePoints = pathToPoints(shape.attr('path'));
    } else {
      var shapeBBox = shape.getCanvasBBox();
      shapePoints = toPoints(shapeBBox);
    }

    return isPolygonsIntersect(points, shapePoints);
  });
  return rst;
}
/**
 *  View 
 * @param view View/Chart
 * @ignore
 */

export function getComponents(view) {
  return view.getComponents().map(function (co) {
    return co.component;
  });
}
/** @ignore */

export function distance(p1, p2) {
  var dx = p2.x - p1.x;
  var dy = p2.y - p1.y;
  return Math.sqrt(dx * dx + dy * dy);
}
/** @ignore */

export function getSpline(points, z) {
  if (points.length <= 2) {
    return getLinePath(points, false);
  }

  var first = points[0];
  var arr = [];
  each(points, function (point) {
    arr.push(point.x);
    arr.push(point.y);
  });
  var path = catmullRom2bezier(arr, z, null);
  path.unshift(['M', first.x, first.y]);
  return path;
}
/**
 * 
 * @param box 
 * @param point 
 * @ignore
 */

export function isInBox(box, point) {
  return box.x <= point.x && box.maxX >= point.x && box.y <= point.y && box.maxY > point.y;
}
/**
 *  view  views
 * @param view  view
 * @returns  views
 * @ignore
 */

export function getSilbings(view) {
  var parent = view.parent;
  var siblings = null;

  if (parent) {
    siblings = parent.views.filter(function (sub) {
      return sub !== view;
    });
  }

  return siblings;
}

function point2Normalize(view, point) {
  var coord = view.getCoordinate();
  return coord.invert(point);
}
/**
 *  view  view 
 * @param view  view
 * @param sibling  view
 * @param point 
 * @ignore
 */


export function getSiblingPoint(view, sibling, point) {
  var normalPoint = point2Normalize(view, point);
  return sibling.getCoordinate().convert(normalPoint);
}
/**
 *  view 
 *  view  includes
 *  x, y  isEqual 
 * 
 * @param records
 * @param record
 * @param xFiled
 * @param yField
 * @returns
 * @ignore
 */

export function isInRecords(records, record, xFiled, yField) {
  var isIn = false;
  each(records, function (r) {
    if (r[xFiled] === record[xFiled] && r[yField] === record[yField]) {
      isIn = true;
      return false;
    }
  });
  return isIn;
} //  field  scale view  view

export function getScaleByField(view, field) {
  var scale = view.getScaleByField(field);

  if (!scale && view.views) {
    each(view.views, function (subView) {
      scale = getScaleByField(subView, field);

      if (scale) {
        return false; // 
      }
    });
  }

  return scale;
}