import { doScaleAnimate } from './util';
/**
 * @ignore
 * 
 * x 
 * @param element 
 * @param animateCfg 
 * @param cfg 
 */

export function growInX(element, animateCfg, cfg) {
  var coordinate = cfg.coordinate,
      minYPoint = cfg.minYPoint;
  doScaleAnimate(element, animateCfg, coordinate, minYPoint, 'x');
}
