//  Coordinate map
var COORDINATE_MAP = {};
/**
 *  coordinate 
 * @param type
 */

export var getCoordinate = function getCoordinate(type) {
  return COORDINATE_MAP[type.toLowerCase()];
};
/**
 *  coordinate 
 * @param type
 * @param ctor
 */

export var registerCoordinate = function registerCoordinate(type, ctor) {
  //  map 
  COORDINATE_MAP[type.toLowerCase()] = ctor;
};